const Style = {
  root: {
    display: 'flex',
    height: '100%'
  },
  childRoot: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    overflow: 'auto',
    padding: '16px',
    boxSizing: 'border-box',
    backgroundColor: 'rgb(90.6%, 91.7%, 91.4%)'
  }
}

export default Style


