const Style = {
  root: {
    display: 'grid',
    gridTemplateColumns: 'minmax(128px, 256px) 1fr',
    gridTemplateRows: 'minmax(96px, 128px) minmax(128px, auto)',
    minHeight: '250px',
    maxHeight: 'auto',
    background: 'white',
    borderRadius: '4px',
  }
}

export default Style


